
import * as React from 'react';
import Grid from '@material-ui/core/Grid';

import './../style.scss';
import { MemberDataSection } from '../MemberDataSection';
import { API } from './../API';
import { ConfinementSection } from './Components/ConfinementSection';
import { AvailmentSection } from './Components/AvailmentSection';
import { TicketInfoSection } from './Components/TicketInfoSection';
import { TicketData, MemberData, DropdownChoice, ConfinementDetails, InquiryData, AvailmentDetails, ConsultationDetails } from '../Models';
import { InquirySection } from './Components/InquirySection';
import { Payloads } from '../Payloads';
import { ConsultationSection } from './Components/ConsultationSection';
import { Processmaker } from '../Processmaker';
import { EmergencyRoomSection } from './Components/EmergencyRoomSection';
import { TicketFooter } from '../TicketFooter';
import { Loading } from '../Components/Loading';
import { Snackbar } from '@material-ui/core';
// import { IconButton } from '../Components/IconButton';
// import { faTimes, faTimesCircle } from '@fortawesome/free-solid-svg-icons';

interface IProps {
    history: any;
}

interface IStates {
    createTicketAttemp: boolean;
    isIcd10CodesLoaded: boolean;
    loadedResources: number;
    snackbarMessage: string | null;
    lastSave?: Date;

    /* Can save to tmp */
    member?: MemberData;
    ticket: TicketData;
    availment: AvailmentDetails;
    confinement: ConfinementDetails;
    consultation: ConsultationDetails;
    inquiries: InquiryData[];
}

export class CreateTicketPage extends React.Component<IProps, IStates> {

    static RESOURCES_TO_LOAD = 1;

    icd10Codes: DropdownChoice[];
    cptCodes: DropdownChoice[];
    facilities: DropdownChoice[];
    doctors: DropdownChoice[];
    agents: DropdownChoice[];
    
    constructor(props: IProps) {
        super(props);

        this.state = {
            createTicketAttemp: false,
            isIcd10CodesLoaded: false,
            snackbarMessage: null,
            loadedResources: 0,

            ticket: {
                ticketChannel: 'Call',
                transactionNo: '9999-9999',
                ticketSource: '',
                memberName: '',
                clientName: '',
                brokerName: '',
                facility: { value: '', label: '' },
                department: '',
                callerName: '',
                contactNo: '',
                ticketType: '',
                specificType: '',
                urgency: 'High',
                agent: { id: '', name: '' },
                additionalNotes: '',
                additionalFiles: [],
                ticketNo: '',
                pmCaseId: '',
                status: 'Open',
                createdBy: { 
                    id: localStorage['user_id'], 
                    name: `${localStorage['first_name']} ${localStorage['last_name']}` 
                },
                dateCreated: new Date(),
                dateUpdated: new Date(),
                dateEscalated: null
            },
            availment: {
                provider: { value: '', label: '' },
                diagnoses: []
            },
            confinement: {
                provider: { value: '', label: '' },
                dateAdmitted: new Date(),
                confinementKind: '',
                roomType: '',
                costPerDay: 0,
                doctor: { value: '', label: '' },
                initialDiagnosis: { value: '', label: '' },
                amount: 0
            },
            consultation: {
                provider: { value: '', label: '' },
                diagnosis: { value: '', label: '' },
                doctor: { value: '', label: '' },
                notes: '',
                assessmentRemarks: '',
                assessmentType: ''
            },
            inquiries: []
        };

        this.facilities = [];
        this.agents = [];
        this.doctors = [];
        this.cptCodes = [];
        this.icd10Codes = [];
    }

    componentDidMount() {
        API.get('facilities',
            (response) => {
                this.facilities = response.map(facility => {
                    return { 
                        value: facility.facility_id,
                        label: facility.name 
                    }
                });
            },
            (error) => {
                console.log(error);
            });



        const tempIcd10Codes = localStorage.getItem('icd10_codes');
        if (!tempIcd10Codes) {
            API.get('icd10-codes?filter[limit]=1000',
                (response) => {
                    this.icd10Codes = response.map(icd10Code => {
                        return { 
                            value: icd10Code.full_code,
                            label: icd10Code.abbreviated_description 
                        };
                    });
                    localStorage.setItem('icd10_codes', JSON.stringify(this.icd10Codes));
                    this.setState({isIcd10CodesLoaded: true});
                },
                (error) => {
                    console.log(error);
                });
        } else {
            this.icd10Codes = JSON.parse(tempIcd10Codes);
            console.log("Using the local copy of ICD10 Codes");
        }
        


        const tempCptCodes = localStorage.getItem('cpt_codes');
        if (!tempCptCodes) {
            API.get('cpts',
                (response) => {
                    this.cptCodes = response.map(cptCode => {
                        return {
                            value: cptCode.code.toString(),
                            label: cptCode.label 
                        };
                    });
                    localStorage.setItem('cpt_codes', JSON.stringify(this.cptCodes));
                }, 
                (error) => {
                    console.log(error);
                });
        } else {
            this.cptCodes = JSON.parse(tempCptCodes);
            console.log("Using the local copy of CPT Codes");
        }
        


        API.get('doctors',
            (response) => {
                this.doctors = response.map(doctor => {
                    return {
                        value: doctor._id,
                        label: ('Dr. ' + doctor.first_name +  ' ' + doctor.last_name),
                        params: [doctor.type]
                    };
                });
            }, 
            (error) => {
                console.log(error);
            });

        API.get(`tmp?filter[where][and][0][user_id]=${localStorage['user_id']}&filter[where][and][1][module]=Customer%20Care`,
            (response) => {
                console.debug(response);
                response = response.filter(item => item.process === 'Create Ticket');
                if (response.length > 0) {
                    this.setState({
                        lastSave: new Date(response[0].timestamp),
                        member: response[0].data.member,
                        ticket: response[0].data.ticket,
                        availment: response[0].data.availment,
                        confinement: response[0].data.confinement,
                        consultation: response[0].data.confinement,
                        inquiries: response[0].data.inquiries
                    });
                }
                this.setState({loadedResources: this.state.loadedResources + 1});
            },
            (error) => {
                console.log(error);
                this.setState({loadedResources: this.state.loadedResources + 1});
            });
    }

    render(): JSX.Element {

        return (
            <>
            <Grid container className='customer-care-main'>

                {this.state.loadedResources !== CreateTicketPage.RESOURCES_TO_LOAD ? (
                    <div 
                        style={{
                            backgroundColor: '#00000055', 
                            zIndex: 100, 
                            position: 'fixed', 
                            width: '100%', 
                            height: '100%'}}>
                        <Loading height='15rem' size={100}/>
                    </div>
                    
                ) : (null)}

                <Snackbar
			        anchorOrigin={{
			          vertical: 'top',
			          horizontal: 'right',
			        }}
			        open={this.state.snackbarMessage ? true : false}
			        autoHideDuration={6000}
			        onClose={() => {
                        this.setState({snackbarMessage: null});
                    }}
                    message={this.state.snackbarMessage} />


                <MemberDataSection 
                    member={this.state.member}
                    ticketStatus='Create'
                    promptChooseMember={this.state.createTicketAttemp}
                    onUpdateMemberStateChange={(member: MemberData) => {
                        this.setState({member: member});
                    }} />
                
                <TicketInfoSection 
                    ticket={this.state.ticket} 
                    facilities={this.facilities}
                    agents={this.agents}
                    createTicketAttemp={this.state.createTicketAttemp}
                    refreshTicketState={() => {
                        this.setState({ticket: Object.assign({}, this.state.ticket)});
                    }} />

                {this.state.ticket.ticketType === 'Availment' ? (
                
                    this.state.ticket.specificType === 'Outpatient Procedure' ? (  
                        <AvailmentSection
                            availment={this.state.availment}
                            facilities={this.facilities}
                            doctors={this.doctors}
                            icd10Codes={this.icd10Codes}
                            cptCodes={this.cptCodes}
                            createTicketAttemp={this.state.createTicketAttemp}
                            refreshAvailmentState={() => {
                                this.setState({availment: Object.assign({}, this.state.availment)});
                            }} />

                    ) : this.state.ticket.specificType === 'Inpatient' ? ( 
                        <ConfinementSection
                            confinement={this.state.confinement}
                            facilities={this.facilities}
                            doctors={this.doctors}
                            cptCodes={this.icd10Codes}
                            createTicketAttemp={this.state.createTicketAttemp}
                            refreshConfinementState={() => {
                                this.setState({confinement: this.state.confinement});
                            }} />
                    ) : this.state.ticket.specificType === 'Emergency Room' ? (
                        <EmergencyRoomSection
                            availment={this.state.availment}
                            facilities={this.facilities}
                            doctors={this.doctors}
                            icd10Codes={this.icd10Codes}
                            cptCodes={this.cptCodes}
                            createTicketAttemp={this.state.createTicketAttemp}
                            refreshAvailmentState={() => {
                                this.setState({availment: this.state.availment});
                            }} />
                        
                    ) : this.state.ticket.specificType === 'Consultation' ? ( 
                        <ConsultationSection
                            consultation={this.state.consultation}
                            facilities={this.facilities}
                            doctors={this.doctors}
                            icd10Codes={this.icd10Codes}
                            createTicketAttemp={this.state.createTicketAttemp}
                            refreshConsultationState={() => {
                                this.setState({consultation: this.state.consultation});
                            }} />
                    ) : (null)

                ) : this.state.ticket.ticketType === 'Inquiry' ? (
                    <InquirySection
                        inquiries={this.state.inquiries}
                        refreshInquiryState={() => {
                            this.setState({inquiries: this.state.inquiries});
                        }} />
                ) : (null)}
            </Grid>

            <TicketFooter 
                lastSave={this.state.lastSave}
                forceShowResolveButton={this.state.ticket.ticketType === 'Inquiry' ? true : false}
                onSaveForNow={() => {
                    const payload = {
                        user_id: localStorage['user_id'],
                        module: 'Customer Care',
                        process: 'Create Ticket',
                        timestamp: new Date(),
                        data: {
                            member: this.state.member,
                            ticket: this.state.ticket,
                            availment: this.state.availment,
                            confinement: this.state.confinement,
                            consultation: this.state.confinement,
                            inquiries: this.state.inquiries
                        }
                    };
                    console.debug('Saving this: ', payload);
                    API.post('tmp', JSON.stringify(payload),
                        (response) => {
                            console.log(response);
                            if (response._id) {
                                this.setState({
                                    snackbarMessage: 'Progress saved.',
                                    lastSave: new Date()
                                });
                            } else {
                                this.setState({snackbarMessage: 'Failed to save progress.'});
                            }
                        },
                        (error) => {
                            console.log(error);
                            this.setState({snackbarMessage: 'Failed to save progress.'});
                        });
                }} 
                onSubmitTicket={() => {
                    this.setState({createTicketAttemp: true});
                    const ticket = this.state.ticket;
                    const member = this.state.member;
                    const confinement = this.state.confinement;
                    const availment = this.state.availment;
                    const consultation = this.state.consultation;
                    const inquiries = this.state.inquiries;
                    
                    /* Must have target member */
                    if (!member) return;

                    /* Confinement Details must have all contents */
                    if (ticket.ticketType === 'Availment' && ticket.specificType === 'Inpatient') {
                        if (confinement.provider.value === '' ||
                            confinement.confinementKind === '' ||
                            confinement.costPerDay === 0 ||
                            confinement.doctor.value === '' ||
                            confinement.initialDiagnosis.value === '' ||
                            confinement.roomType === '') {
                            return;
                        }
                    }

                    const payload = {
                        tmp: {
                            user_id: localStorage['user_id'],
                            module: 'Customer Care',
                            process: 'Create Ticket',
                            timestamp: new Date()
                        },
                        ...Payloads.getPostTicketPayload(ticket, member, confinement, availment, inquiries, consultation)
                    };
                        
                    console.debug(JSON.stringify(payload));

                    API.post('customer-care/tickets', JSON.stringify(payload),
                        (postTicketResponse) => { 
                            console.log(postTicketResponse);
                            if (postTicketResponse['_id']) {
                                console.log('Ticket No: '+postTicketResponse['id']);
                                let is_availment = false;
                                if (this.state.ticket.ticketType === 'Availment'){
                                    is_availment=true;
                                }
                                let trigger_id = "3905447335db2a32daafd33048658514"; //should refer to redux createtickettrigger_id!!!
                                let body = {
                                    "pro_uid" : "1538417025dae8dae9ff7a9016454302", //should refer to redux createticketproject_id!!!
                                    "tas_uid":"6092309955dae8db0a85907021073537",   //should refer to redux createticketstarttask_id!!!
                                    "ticket_no":postTicketResponse['_id'],
                                    "can_be_resolved_imm":0,
                                    "is_availment":is_availment
                                };
                                console.log('Body:');
                                console.log(body);
                                //Start Case
                                Processmaker.post('cases', body,
                                    (casesResponse) => {
                                        if (casesResponse['app_uid']) {
                                            let case_id = casesResponse['app_uid'];
                                            //Trigger Case
                                            Processmaker.put(`cases/${case_id}/execute-trigger/${trigger_id}`, body,
                                                (triggerResponse) => {
                                                    console.log('Trigger Response:');
                                                    console.log(triggerResponse);

                                                    Processmaker.put(`cases/${case_id}/variable`, body,
                                                        (variableResponse) => {
                                                            console.log('Set Variable Response:');
                                                            console.log(variableResponse);

                                                            API.patch(`customer-care/tickets/${postTicketResponse._id}`, { pmaker_case_uid: case_id },
                                                                (patchTicketResponse) => {
                                                                    console.log('Patch Response:');
                                                                    console.log(patchTicketResponse);
                                                                    this.props.history.push('#/customer-care/');
                                                                    window.location.reload();
                                                                },
                                                                (error) => {
                                                                    console.log(error);
                                                                }
                                                            );
                                                        }, 
                                                        (error) => {
                                                            console.log(error);
                                                        }
                                                    );
                                                }, 
                                                (error) => {
                                                    console.log(error);
                                                }
                                            );
                                        }
                                    }, 
                                    (error) => {
                                        console.log(error);
                                    }
                                ); 
                            }     
                        },
                        (error) => {
                            console.log(error);
                        });
                }}
                onResolveTicket={() => {
                    alert('No implementation yet');
                }} />
            </>
        );
    }
}